html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  background-color: #000;
  color: gray;
  font-family: 'Lato'; }

.main {
  max-width: 800px;
  margin: 32px auto;
  width: 100%; }
  @media (max-width: 640px) {
    .main {
      margin-top: 0; } }
  @media (min-width: 1200px) {
    .main {
      max-width: 1000px;
      margin: 32px auto;
      width: 100%; } }

@media (max-width: 640px) {
  .navigation__button__X {
    border: none;
    position: absolute;
    top: 0;
    right: 16px;
    background-color: #fefefe;
    height: 37px;
    width: 44px;
    font-size: 38px;
    font-weight: bold;
    z-index: 1; } }

@media (min-width: 641px) {
  .navigation__button__X {
    display: none; } }

@media (max-width: 640px) {
  .navigation__button__X--visible {
    z-index: 3; } }

@media (max-width: 640px) {
  .navigation__button--notVisible {
    display: none; } }

@media (max-width: 640px) {
  .navigation__button {
    border: none;
    position: absolute;
    top: 0;
    right: 16px;
    background-color: #fefefe;
    z-index: 2; } }

@media (min-width: 641px) {
  .navigation__button {
    display: none; } }

.navigation__link {
  color: #7A8450;
  text-decoration: none; }
  .navigation__link:hover {
    color: red; }

.navigation__list {
  align-items: center;
  display: flex;
  justify-content: center;
  list-style: none;
  text-decoration: none;
  padding: 0; }
  @media (max-width: 640px) {
    .navigation__list {
      display: none;
      flex-direction: column;
      justify-content: center;
      margin: 0;
      padding: 4px; } }
  @media (max-width: 640px) {
    .navigation__list--visible {
      display: flex; } }

@media (min-width: 641px) {
  .navigation__icon {
    display: none; } }

.navigation__item {
  display: block;
  margin: 0 16px; }
  @media (max-width: 640px) {
    .navigation__item {
      margin: 16px; } }

@media (max-width: 640px) {
  .navigation {
    background-color: #fefefe;
    margin: 0; } }

.site-header {
  height: 25px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 16px;
  padding-top: 20px; }
  @media (max-width: 640px) {
    .site-header {
      padding: 0;
      top: 0;
      left: 0; } }

.top {
  font-size: 20px;
  position: fixed;
  bottom: 10px;
  right: 10px;
  color: #7A8450;
  text-decoration: none; }

.header__title {
  padding-top: 60px; }

.header__side {
  padding-top: 20px; }

.article__footer {
  margin: 0px;
  padding: 0px; }
  .article__footer-img {
    max-width: 700px;
    width: 100%; }

.article__header {
  color: #B6465F;
  font-size: 30px;
  padding-top: 20px; }

.about {
  max-width: 700px;
  width: 100%; }
  .about__header {
    font-size: 30px;
    color: #B6465F; }
  .about__paragraph {
    margin: 0px;
    padding: 0px; }

.diary__header {
  color: #B6465F;
  font-size: 30px; }

.daily {
  font-style: italic;
  padding: 16px; }
  .daily__header {
    color: blueviolet;
    font-size: 20px;
    margin: 0 0 16px; }
  .daily__link {
    color: #7A8450;
    text-decoration: none; }
  .daily__paragraph {
    line-height: 24px;
    margin: 0px;
    padding: 0px; }

.grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 16px;
  grid-row-gap: 16px; }
  @media (max-width: 640px) {
    .grid {
      display: grid;
      grid-template-columns: 1fr;
      grid-column-gap: 16px;
      grid-row-gap: 16px; } }
  @media (min-width: 1200px) {
    .grid {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-column-gap: 16px;
      grid-row-gap: 16px; } }

.footer {
  font-size: 10px; }
